export enum SortNameType {
  POPULAR_DESC = " ( )",
  POPULAR_ASC = " ( )",
  PRICE_DESC = " ( )",
  PRICE_ASC = " ( )",
  ALPHABET_DESC = " ( )",
  ALPHABET_ASC = " ( )",
}

export enum SortType {
  POPULAR = "popular",
  PRICE = "price",
  NAME = "name",
}

export enum OrderType {
  DESC = "desc",
  ASC = "asc",
}

export type SortByType = {
  name?: SortNameType;
  type?: SortType;
  order?: OrderType;
};

export interface FilterSliceType {
  category: number;
  sortBy: SortByType;
  currentPage: number;
  searchValue?: string;
}